.input::-webkit-outer-spin-button,
.input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.input[type="number"] {
  appearance: textfield;
}

.input {
  @include font-form;
  @include input-number;
  display: flex;
  padding: 13px 20px 14px;
  border: 1px solid #e7e7e7;
  flex-grow: 1;
  border-radius: 0;
  min-width: 130px;

  @media (min-width: $tablet-width) {
    padding: 10px 20px;
    font-size: 20px;
    line-height: 30px;
  }

  &::placeholder {
    color: #444444;
  }

  &:hover {
    border-color: #cdcdcd;
  }

  &:focus-visible {
    outline: 2px solid #444444;
  }

  &:invalid:not(:placeholder-shown) {
    outline: 2px solid #ff8282;
  }

  &:disabled {
    opacity: 30%;
  }
}

.input--age {
  width: 10px;
}

.input--email {
  position: relative;
  border-color: #cdcdcd;

  &:hover {
    border-color: #e7e7e7;
  }

  &:focus ~ .contact-field__email {
    color: #68b738;
  }

  &:invalid:not(:placeholder-shown) ~ .contact-field__email {
    color: #ff8282;
  }
}

.contact-field__email {
  position: absolute;
  content: "";
  right: 16px;

  @media (min-width: $tablet-width) {
    right: 18px;
  }
}

.input--tel {
  position: relative;
  border-color: #cdcdcd;

  &:hover {
    border-color: #e7e7e7;
  }

  &:focus ~ .contact-field__tel {
    color: #68b738;
  }

  &:invalid:not(:placeholder-shown) ~ .contact-field__tel {
    color: #ff8282;
  }
}

.contact-field__tel {
  position: absolute;
  content: "";
  right: 13px;
  top: 16px;

  @media (min-width: $tablet-width) {
    right: 15px;
  }
}
